body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: black;
  color: white;
  margin: 0 auto;
  font-family: 'Roboto', sans-serif;
}

.anchor {
  display: block;
  position: relative;
  top: -100px;
  visibility: hidden;
}
.header {
  background: black;
  color: white;
  height: 70px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding-left: 10%;
}

@media screen and (max-width: 750px) {
  .header {
      padding-left: 0;
      text-align: center;
  }
}

.menu {
  list-style-type: none;
  margin-left: 55%;
  margin-top: -55px;
}

.menu li {
  float: left;
}

.menu li a {
  padding: 0 20px;
  color: white;
  text-decoration: none;
  border-left: 1px solid white;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.menu li a:hover { 
  background: white;
  color: black;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}


@media screen and (max-width: 750px) { 
  .menu { 
      display: table;
      margin: 0 auto;
  }

  .menu li { 
      background: black;
  }
}
.intro-wrapper { 
  margin-top: 70px;
  padding: 20px 10% 0 10%;
  background-color: #4382A9;
}

.intro {
  display: flex;
  flex-flow: wrap;
}
.intro-text-container {
  flex-basis: 33%;
}

@media screen and (max-width: 750px) {
  .intro-text-container {
    flex-basis: 100%;
    order: 1;
  }
}

.intro-text-container a {
  color: white;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.intro-text-container a:hover {
  background: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.reference-container {
  flex-direction: column;
  flex-basis: 30%;
  display: flex;
  flex-wrap: wrap;
  margin-left: 10px;
  padding: 60px 5px 20px 5px;
}

@media screen and (max-width: 900px) {
  .reference-container {
    flex-basis: 100%;
    margin: 10px auto 0 auto;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 5px 5px 20px 5px;
  }
}

@media screen and (max-width: 750px) {
  .reference-container {
    order: 3;
  }
}

.reference-style {
  margin-top: 10px;
  height: 30px;
  width: auto;
}

.link-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.link-container a {
  flex-basis: 10%;
}

.link-text {
  margin-left: 10px;
  color: white;
  font-size: 16px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  -webkit-text-decoration: underline white;
          text-decoration: underline white;
}

.link-text:hover {
  background: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.headshot-container {
  flex-basis: 30%;
  padding: 10px 20px 20px 0;
}

@media screen and (max-width: 750px) {
  .headshot-container {
    flex-basis: 70%;
    margin: 10px auto 0 auto;
    order: 2;
  }
}

.headshot-container .headshot {
  width: 100%;
  height: auto;
  border-radius: 3px;
  box-shadow: 5px 5px 5px #181818;
}
